
'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation'; // Import useRouter
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import type { AttendanceRecord } from '@/lib/types';
import Image from 'next/image';
import { format } from 'date-fns';
import { History, User, Calendar, Clock, MapPin, Camera } from 'lucide-react';
import { useToast } from '@/hooks/use-toast'; // Import useToast

export default function HistoryPage() {
  const router = useRouter(); // Initialize router
  const { toast } = useToast(); // Initialize toast
  const [history, setHistory] = useState<AttendanceRecord[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Check login status
    const loggedInUserString = localStorage.getItem('loggedInUser');
    if (!loggedInUserString) {
      toast({
        variant: 'destructive',
        title: 'Not Logged In',
        description: 'Please log in to view attendance history.',
      });
      router.push('/login');
      return; // Stop further execution if not logged in
    }

    // Fetch data from localStorage
    const storedHistory = localStorage.getItem('attendanceHistory');
    if (storedHistory) {
      try {
          const parsedHistory: AttendanceRecord[] = JSON.parse(storedHistory).map((record: any) => ({
              ...record,
              timestamp: new Date(record.timestamp) // Ensure timestamp is a Date object
          })).sort((a: AttendanceRecord, b: AttendanceRecord) => b.timestamp.getTime() - a.timestamp.getTime()); // Sort newest first
          setHistory(parsedHistory);
      } catch (e) {
          console.error("Failed to parse attendance history:", e);
          setHistory([]); // Set empty if parsing fails
          toast({ // Inform user about potential data issue
              variant: "destructive",
              title: "History Error",
              description: "Could not load attendance history.",
          });
      }
    }
    setIsLoading(false);
  }, [router, toast]); // Add router and toast to dependency array

  return (
    <Card className="shadow-lg">
      <CardHeader>
        <CardTitle className="text-2xl font-bold text-center text-primary flex items-center justify-center gap-2">
           <History className="h-6 w-6" /> Attendance History
        </CardTitle>
        <CardDescription className="text-center">
          View your past attendance records.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[60vh] rounded-md border">
          <Table>
            <TableCaption>
              {isLoading
                ? 'Loading attendance history...'
                : history.length === 0
                ? 'No attendance records found.'
                : 'A list of your recent attendance records.'}
            </TableCaption>
            <TableHeader className="sticky top-0 bg-secondary">
              <TableRow>
                <TableHead className="w-[150px]"><User className="inline-block mr-1 h-4 w-4" />User</TableHead>
                <TableHead><Calendar className="inline-block mr-1 h-4 w-4" />Date</TableHead>
                <TableHead><Clock className="inline-block mr-1 h-4 w-4" />Time</TableHead>
                <TableHead><MapPin className="inline-block mr-1 h-4 w-4" />Location / Address</TableHead>
                <TableHead className="text-center"><Camera className="inline-block mr-1 h-4 w-4" />Selfie</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {history.map((record) => (
                <TableRow key={record.id}>
                  <TableCell className="font-medium">{record.userName} ({record.userId})</TableCell>
                  <TableCell>{format(record.timestamp, 'PPP')}</TableCell>
                  <TableCell>{format(record.timestamp, 'p')}</TableCell>
                  <TableCell>
                    <div className="flex flex-col gap-1">
                      <span>
                        Lat: {record.location.latitude.toFixed(4)}, Lon: {record.location.longitude.toFixed(4)}
                      </span>
                      {record.location.address ? (
                          <span className="text-xs text-muted-foreground break-words">
                             {record.location.address}
                          </span>
                      ) : (
                           <span className="text-xs text-muted-foreground italic">Address not recorded</span>
                      )}
                    </div>
                  </TableCell>
                  <TableCell className="text-center">
                    {record.selfie ? (
                       <Popover>
                        <PopoverTrigger asChild>
                           <Image
                            src={record.selfie}
                            alt={`Selfie of ${record.userName || 'user'} on ${format(record.timestamp, 'PPP')}`}
                            width={40}
                            height={40}
                            className="rounded-full object-cover inline-block border cursor-pointer hover:opacity-80 transition-opacity"
                          />
                        </PopoverTrigger>
                        <PopoverContent className="w-auto p-0">
                           <Image
                              src={record.selfie}
                              alt={`Full selfie of ${record.userName || 'user'}`}
                              width={200} // Adjust size as needed
                              height={200}
                              className="rounded object-cover"
                          />
                        </PopoverContent>
                       </Popover>
                    ) : (
                      'No Selfie'
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </ScrollArea>
      </CardContent>
    </Card>
  );
}

// Need to import Popover components for the selfie preview
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"

